<template>
   <carousel
        @next="next"
        @prev="prev"
      >
        <carousel-slide v-for="(slide,index) in slides" 
            :key="index" 
            :index="index"
            :visibleSlide="visibleSlide"
            :direction="direction"
            >
          <img :src="slide"/>
        </carousel-slide>
    </carousel>
</template>

<script>
import Carousel from './Carousel'
import CarouselSlide from './CarouselSlide'
export default {
    components: { CarouselSlide,Carousel },
    data(){
    return{
      slides:[
        'https://service.smt.docomo.ne.jp/keitai_payment/common/images/topTsumabo221101.png',
        'https://service.smt.docomo.ne.jp/keitai_payment/common/images/topTmobilekangensai8221027.png',
        'https://service.smt.docomo.ne.jp/keitai_payment/common/images/top_CPSmaina2206.png',
        'https://service.smt.docomo.ne.jp/keitai_payment/common/images/top_CPBstepA2207.png',
        'https://service.smt.docomo.ne.jp/keitai_payment/common/images/topTdyoubi221102.png',
      ],
      visibleSlide:0,
      direction:'left',
    }
  },
  computed:{
    slideLen(){
      return this.slides.length;
    }
  },
  methods:{
    next(){
      if(this.visibleSlide >= this.slideLen - 1){
          this.visibleSlide = 0;
      }else{
        this.visibleSlide++;
      }
      this.direction = "left"
      console.log("next click")
    },
    prev(){
      if(this.visibleSlide <= 0){
          this.visibleSlide = this.slideLen - 1;
      }else{
        this.visibleSlide--;
      }
      this.direction = "right"
      console.log("prev click")
    },
  }
}

</script>

