import { createWebHistory, createRouter } from "vue-router";
import Campaign from './components/Campaign'
import Home from './components/Home'
const routes = [
  {
    path: "/campaign",
    component: Campaign,
  },{
    path: "/",
    component: Home,
  },
  
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 