<template>
  <div>
    <div>
      <div class="cam_kv">
        <img src="https://nttdocomo-ssw.com/keitai_payment/campaign/img/title_img.png" alt="campaign">
      </div>
      <CarouselMain></CarouselMain>
      <div class="btn-wrap">
        <button @click="active1" class="btn1 active">街のお店</button>
        <button @click="active2" class="btn2">ネットのお店</button>
        <button @click="active3" class="btn3">その他</button>
      </div>
      <div>
        <div v-if="step ==0">
          <div class="cpm-list">
            <div v-for="(a,i) in campaignImg" :key="i">
              <img :src="a.url" alt="">
              <div>
                <p>{{a.event}}</p>
                <p>{{a.title}}</p>
                <p>{{a.date}}</p>
              </div>
            </div>
          </div>
        </div>
        <div v-if="step ==1">
            <div class="cpm-list">
            <div v-for="(a,i) in campaignImg" :key="i">
              <img :src="a.url" alt="">
              <div>
                <p>{{a.event}}</p>
                <p>{{a.title}}</p>
                <p>{{a.date}}</p>
              </div>
            </div>
          </div>
        </div>
        <div v-if="step ==2">
          <div class="cpm-list">
            <div v-for="(a,i) in campaignImg" :key="i">
              <img :src="a.url" alt="">
              <div>
                <p>{{a.event}}</p>
                <p>{{a.title}}</p>
                <p>{{a.date}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="closed_cpn">
          <a href="#">終了したキャンペーンはこちら</a>
      </div>
    </div>
  </div>
</template>

<script>
import CarouselMain from './CarouselMain.vue'
import cpnBnr from '../assets/cpnBnr.js'
export default {
  data(){
    return{
      step:0,
      campaignImg:cpnBnr,
    }
  },
  components:{
    CarouselMain,
  },
  methods:{
    
    active1(){
      this.step=0
      var btn1 = document.querySelector('.btn1')
      var btn2 = document.querySelector('.btn2')
      var btn3 = document.querySelector('.btn3')
      btn1.classList.add('active')
      btn2.classList.remove('active')
      btn3.classList.remove('active')
      this.campaignImg.reverse().sort()
    },
    active2(){
      this.step=1
      var btn1 = document.querySelector('.btn1')
      var btn2 = document.querySelector('.btn2')
      var btn3 = document.querySelector('.btn3')
      btn2.classList.add('active')
      btn1.classList.remove('active')
      btn3.classList.remove('active')
      this.campaignImg.sort().reverse()
    },
    active3(){
      this.step=2
      var btn1 = document.querySelector('.btn1')
      var btn2 = document.querySelector('.btn2')
      var btn3 = document.querySelector('.btn3')
      btn3.classList.add('active')
      btn1.classList.remove('active')
      btn2.classList.remove('active')
      this.campaignImg.reverse().sort()
    }
  }
}
</script>

